


<template>
    <div class="location">
        <!-- 头部 -->
        <!-- <Movie-header :type="'movie'"></Movie-header> -->
        <!-- 定位 -->
        <main class="location-main">
            <!-- row -->
            <div class="location-row">
                <!-- 右边列表 -->
                <section class="right-list">
                    <ul>
                        <li v-for="item in location" :key="item.index">
                            <a :href="'#'+item.index">{{item.index}}</a>
                        </li>
                    </ul>
                </section>
                <!-- 选项 -->
                <section class="location-options">
                    <span>热门城市</span>
                </section>
                <!-- 列表 -->
                <section class="location-list">
                    <ul class="location-list-ul">
                        <li v-for="i in rm" :key="i.id" @click="OPEN_location_nm(i.id)">{{i.nm}}</li>
                    </ul>
                </section>
                <!-- 选项 -->
                <section class="location-options opp" v-for="item in location" :key="item.index">
                    <span>
                        <a></a>
                        {{item.index}}
                    </span>
                    <!-- 列表 -->
                    <section class="location-list">
                        <ul class="location-list-ul">
                            <li
                                v-for="i in item.list"
                                :key="i.id"
                                @click="OPEN_location_nm(i.id)"
                            >{{i.nm}}</li>
                        </ul>
                    </section>
                </section>
            </div>
            <!-- row - end -->
        </main>
        <!-- 脚注 - 导航 -->
        <Movie-footer></Movie-footer>
    </div>
</template>


<script>
import header from "@/components/Movie-header";
import footer from "@/components/Movie-footer";

export default {
    name: "location",
    components: {
        "Movie-header": header,
        "Movie-footer": footer
    },
    data() {
        return {
            rm: [],
            location: []
        };
    },
    methods: {
        // 状态管理 - 修改地址
        OPEN_location_nm(id) {
            this.location.filter(item => {
                // console.log(item.list);
                item.list.forEach(val => {
                    // console.log(val.id === id);
                    if (val.id === id) {                      
                        // 修改位置
                        this.$store.commit("OPEN_location", val);
                        // 返回上一页
                        this.$router.back(-1);
                    }
                });
            });
        },

        // 获取城市
        OPEN_Criyt() {
            this.axios
                .get(`/api/cityList`)
                .then(res => {
                    // console.log(res.data.data.cities);
                    // 热门城市
                    let arr = res.data.data.cities;
                    let { rm_arr, box_arr } = this.OPEN_sort(arr);
                    this.rm = rm_arr;
                    this.location = box_arr;
                })
                .catch(err => {
                    console.log(err);
                });
        },

        // 排序
        OPEN_sort(array) {
            let rm_arr = [];
            let box_arr = [];

            // 热门城市
            for (let i in array) {
                if (array[i].isHot === 1) {
                    rm_arr.push(array[i]);
                }
            }

            // 重组城市
            for (let i in array) {
                let filters = array[i].py.substring(0, 1).toUpperCase();
                if (toCom(filters)) {
                    box_arr.push({
                        index: filters,
                        list: [{ nm: array[i].nm, id: array[i].id }]
                    });
                } else {
                    for (let j in box_arr) {
                        if (box_arr[j].index === filters) {
                            box_arr[j].list.push({
                                nm: array[i].nm,
                                id: array[i].id
                            });
                        }
                    }
                }
            }

            // 去重复方法
            function toCom(str) {
                for (let i in box_arr) {
                    if (box_arr[i].index === str) return false;
                }
                return true;
            }

            // 排序
            box_arr.sort((n1, n2) => {
                if (n1.index > n2.index) return 1;
                else if (n1.index < n2.index) return -1;
                return 0;
            });
            // console.log(rm_arr, box_arr);
            return { rm_arr, box_arr };
        },

        OPEN_Click() {}
    },
    mounted() {
        this.OPEN_Criyt();
    }
};
</script>


<style scoped>
.header {
    position: static;
}
.location-main {
    background-color: #f5f5f5;
}

/* 选项 */
.location-options {
    padding-left: 15px;
    line-height: 30px;
    background-color: #ebebeb;
    text-align: left;
}
/* 列表 */
.location-list {
    background-color: #f5f5f5;
    padding: 4%;
    box-sizing: border-box;
}
.location-list-ul {
    display: flex;
    flex-wrap: wrap;
}
.location-list-ul > li {
    width: 28%;
    padding: 1%;
    box-sizing: border-box;
    background-color: #fff;
    border: 1px solid #e5e5e5;
    border-radius: 5px;
    margin: 2%;
    text-align: center;
}
.right-list {
    position: fixed;
    width: 2rem;
    /* background-color: #fff; */
    right: 0;
    height: auto;
}
.right-list li {
    line-height: 1.7rem;
}
.opp {
    padding-left: 0;
}
.opp > span {
    padding-left: 15px;
}
</style>
