
<template>
    <div class="Movie-header">
        <div class="header">
            <section class="nav">
                <h1>{{ title }}</h1>
            </section>
            <!-- 电影 - bottom -->
            <section v-if="type === 'movie'" class="Movie-bottom">
                <ul class="Movie-bottom-ul">
                    <li>
                        <router-link to="/movie/location">{{ location }}▾</router-link>
                    </li>
                    <li>
                        <router-link to="/movie">正在热映</router-link>
                    </li>
                    <li>
                        <router-link to="/movie/upcoming">即将上映</router-link>
                    </li>
                    <li class="serch">
                        <router-link to="/search">
                            <img src="@/assets/serch.png" />
                        </router-link>
                    </li>
                </ul>
            </section>
            <!-- 影院 - bottom -->
            <section v-if="type === 'cinema'" class="cinema-bottom">
                <ul class="cinema-bottom-ul">
                    <li>
                        <router-link to="/movie/location">{{ location }}▾</router-link>
                    </li>
                    <li>
                        <router-link to="/search" tag="div" class="search-input">
                            <img src="@/assets/serch-rgba.png" height="13" />
                            搜影院
                        </router-link>
                    </li>
                </ul>
                <!-- 筛选 -->
                <section class="cinema-screening">
                    <div>全城▾</div>|
                    <div>品牌▾</div>|
                    <div>特色▾</div>
                </section>
            </section>
        </div>
    </div>
</template>


<script>
export default {
    name: "Movie-header",
    data() {
        return {
            // type: ""
        };
    },
    methods: {
        // 判断高度来定义父级高度
        OPEN_height() {
            const MOVIE_HEADER = document.querySelector(".Movie-header");
            const HEADER = document.querySelector(".header");
            // 子级高度
            let height = HEADER.clientHeight || HEADER.offsetHeight;
            MOVIE_HEADER.style.height = height + "px";
        }
    },
    mounted() {
        // 判断高度来定义父级高度
        setTimeout(() => {
            this.OPEN_height();
        }, 1);
    },
    props: {
        title: {
            default: "猫眼电影"
        },
        type: {
            default: ""
        }
    },
    computed: {
        location() {
            return this.$store.state.location;
        }
    }
};
</script>


<style scoped>
.Movie-header {
    /* height: 103px; */
    overflow: hidden;
}
.header {
    position: fixed;
    top: 0;
    width: 100%;
    height: auto;
    z-index: 10;
}
.nav {
    text-align: center;
    background: #e54847;
    border-bottom: 1px solid #e54847;
}
.nav > h1 {
    color: #fff;
    font-size: 1.2rem;
    padding: 15px;
    /* font-family: "黑体"; */
    font-weight: 100;
}
/* serch */
.serch img {
    margin: auto;
}
/* 电影 - bottom */
.Movie-bottom,
.cinema-bottom {
    border-bottom: 1px solid #e5e5e5;
    background-color: #fff;
    z-index: 99999999;
}
.Movie-bottom-ul,
.cinema-bottom-ul {
    display: flex;
}
.Movie-bottom-ul > li {
    padding: 10px 0;
    width: 25%;
    font-family: "黑体";
}
/* 影院 - botoom */
.cinema-bottom-ul {
    background-color: #f5f5f5f5;
}
.cinema-bottom-ul > li {
    width: 25%;
    padding: 10px 0;
}
.cinema-bottom-ul > li:nth-child(2) {
    width: 75%;
}
.search-input {
    display: flex;
    flex-grow: 1;
    -webkit-box-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: center;
    height: 28px;
    font-size: 13px;
    color: #b2b2b2;
    margin-left: 18px;
    border: 0.5px solid #e6e6e6;
    background-color: #fff;
    border-radius: 5px;
    margin-right: 15px;
}
.cinema-screening {
    display: flex;
    padding: 1rem 0;
    color: #999;
    font-size: 0.9rem;
}
.cinema-screening > div {
    width: 33%;
}
</style>

